
.flexwrap {
    display: flex;
    justify-content: center;
}

.contentwrap {
    margin: 10px;
    width: 70%;
    max-width: 700px;
}

.h1-text {
    color: darkslategrey;
    font-size: 3rem;
    margin: 20px;
    margin-left: 2px;
}

.h2-text {
    color: darkslategrey;
    font-size: 1.5rem;
    margin: 20px;
    margin-left: 2px;
}

.h3-text {
    color: darkslategrey;
    font-size: 1rem;
    margin: 20px;
    margin-left: 2px;
}

.list-text {
    color: darkslategrey;
    font-size: 1rem;
    margin-left:30px;
}

.figcaption-text {
    color: darkslategrey;
    font-size: 1rem;
    margin-left: 2px;
    font-style: italic;
}

.log-text {
    font-size: 1.3rem;
}

.inline-img {
    margin-top:20px;
    margin-bottom:20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}